import React, { useState } from 'react'
import styled from 'styled-components'
import ComponentHeader from '../ComponentHeader/ComponentHeader'
import { DefaultInput, DateInput, DefaultTextarea } from '../../atoms/Input'
import { ProceedingBtn } from '../../atoms/Button'
import { updateData } from '../../../utils'
import RequireInput from '../../atoms/Input/RequireInput'

export default function Career({
  idx,
  career,
  careerData,
  setCareerData,
  handleDelete,
  activeAccordion,
  onAccordionClick,
}) {
  const [isStill, setIsStill] = useState(career.inProgress)

  return (
    <ComponentHeader
      id={career.id}
      idx={idx}
      kind={''}
      title={career.title ? career.title : null}
      handleDelete={handleDelete}
      isOpen={idx === activeAccordion}
      onAccordionClick={() => onAccordionClick(idx)}
    >
      <Wrap>
        <div>
          <div className="firstSectionWrap">
            <RequireInput
              id="title"
              type="text"
              width="100%"
              name="title"
              placeholder=") (WENIV)"
              onChange={(e) => {
                updateData(e, idx, careerData, setCareerData)
              }}
              inputData={career.title}
              maxLength={45}
            >
              {''}
            </RequireInput>
            <DefaultInput
              id="rank"
              type="text"
              name="rank"
              placeholder=") "
              onChange={(e) => {
                updateData(e, idx, careerData, setCareerData)
              }}
              inputData={career.rank}
            >
              {''}
            </DefaultInput>
          </div>
        </div>
        <DateWrap>
          <DateInput
            id="startDate"
            width="220px"
            name="startDate"
            inputData={career.startDate}
            onChange={(e) => {
              updateData(e, idx, careerData, setCareerData)
            }}
          >
            {''}
          </DateInput>
          <Tilde>~</Tilde>
          <DateInput
            id="endDate"
            width="220px"
            name="endDate"
            inputData={career.endDate}
            onChange={(e) => {
              updateData(e, idx, careerData, setCareerData)
            }}
            isStill={isStill}
          >
            {''}
          </DateInput>

          <ProceedingBtn
            name="inProgress"
            value={isStill}
            type="career"
            idx={career.id}
            inputData={career.inProgress}
            onChange={(e) => {
              setCareerData(
                careerData.map((el, i) =>
                  i === idx ? { ...el, inProgress: !isStill } : el
                )
              )
              setIsStill(!isStill)
            }}
            isStill={isStill}
          />
        </DateWrap>
        {/*    */}
        <DefaultTextarea
          name="works"
          placeholder={')  Front-End '}
          onChange={(e) => {
            updateData(e, idx, careerData, setCareerData)
          }}
          inputData={career.works}
          lineHeight="21px"
        >
          {' '}
        </DefaultTextarea>
      </Wrap>
    </ComponentHeader>
  )
}

const Wrap = styled.div`
  display: flex;
  flex-direction: column;
  gap: 24px;

  div.firstSectionWrap {
    display: grid;
    align-items: start;
    grid-template-columns: 506px 220px;
    gap: 12px;
  }
`

const DateWrap = styled.div`
  display: flex;
  align-items: center;
`

const Tilde = styled.p`
  color: var(--surface-color);
  font-size: 14px;
  font-weight: 500;
  margin: 20px 8px 0 8px;
`
